.datatable {
  padding: 16px 16px 32px 16px;
  height: 600px;
  div.css-z8fhq1-MuiDataGrid-columnHeaders {
    background-color: #fff4f0;
  }

  .date-container {
    gap: 8px;
  }
  .date-picker {
    margin-bottom: 17px;
    display: flex;
    justify-content: flex-end;
    div.ant-picker {
      height: 40px;
    }
    div.ant-picker-input {
      input {
        &::placeholder {
          color: black;
        }
      }
    }
    .action-control {
      margin-right: 16px;
      display: flex;
      gap: 10px;
      button {
        border: none;
        outline: none;
        color: #1976d2;
        padding: 6px 8px;
        font-weight: 500;
        font-size: 0.875rem;
        background-color: transparent;
        cursor: pointer;
        &:hover {
          background-color: rgba(25, 118, 210, 0.04);
        }
      }
    }
  }

  div.MuiDataGrid-footerContainer {
  }
  .datatableTitle {
    width: 100%;
    font-size: 24px;
    color: gray;
    margin-bottom: 10px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    gap: 15px;

    .link {
      text-decoration: none;
      color: green;
      font-size: 16px;
      font-weight: 400;
      border: 1px solid green;
      padding: 5px 10px;
      border-radius: 5px;
      cursor: pointer;
      &:hover {
        color: white;
        background-color: green;
      }
    }
  }

  .cellWithImg {
    display: flex;
    align-items: center;

    .cellImg {
      width: 32px;
      height: 32px;
      border-radius: 50%;
      object-fit: cover;
      margin-right: 20px;
    }
  }

  .cellWithStatus {
    padding: 5px;
    border-radius: 5px;

    &.active {
      background-color: rgba(0, 128, 0, 0.05);
      color: green;
    }
    &.pending {
      background-color: rgba(255, 217, 0, 0.05);
      color: goldenrod;
    }
    &.passive {
      background-color: rgba(255, 0, 0, 0.05);
      color: crimson;
    }
  }

  .cellAction {
    display: flex;
    align-items: center;
    gap: 15px;

    .viewButton {
      padding: 2px 5px;
      border-radius: 5px;
      color: darkblue;
      border: 1px dotted rgba(0, 0, 139, 0.596);
      cursor: pointer;
    }

    .deleteButton {
      padding: 2px 5px;
      border-radius: 5px;
      color: crimson;
      border: 1px dotted rgba(220, 20, 60, 0.6);
      cursor: pointer;
    }

    .editButton {
      padding: 2px 5px;
      border-radius: 5px;
      color: orange;
      border: 1px solid orange;
      cursor: pointer;
    }
  }
}

.moadl-container {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: rgba(0, 0, 0, 0.6);
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  .from-container {
    width: 50%;
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 50px;

    form {
      width: 100%;
      // box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.6);
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 20px;
      .control-group {
        width: 100%;
        display: flex;
        gap: 30px;
        .from-control {
          flex: 1;
          input {
            width: 100%;
            outline: none;
            border: none;
            border-bottom: 2px solid #008080;
            font-size: 18px;
            font-weight: 600;
            padding: 10px;
          }
          select {
            width: 100%;
            padding: 10px;
            font-size: 18px;
            font-weight: 600;
          }
        }
        textarea {
          width: 100%;
          font-size: 16px;
        }
      }

      .from-action {
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 30px;
        gap: 15px;
        .action-btn {
          width: 150px;
          font-size: 18px;
          font-weight: 600;
          border: none;
          outline: none;
          background-color: #008080;
          color: white;
          padding: 10px;
          // border-radius: 5px;
          cursor: pointer;
        }
        .action-btn-cancel {
          width: 150px;
          font-size: 18px;
          font-weight: 600;
          border: none;
          outline: none;
          background-color: red;
          color: white;
          padding: 10px;
          // border-radius: 5px;
          cursor: pointer;
        }
      }
    }
  }

  .progress-container {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.4);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
  }

  .message-container {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.4);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
    .message {
      padding: 50px;
      background-color: white;
      border-radius: 5px;
      display: flex;
      align-items: center;
      flex-direction: column;
      gap: 30px;
      .content {
        display: flex;
        align-items: center;
        gap: 10px;
        .icon {
          color: green;
          font-size: 24px;
        }
        p {
          font-size: 24px;
          font-weight: 600;
        }
      }
      button {
        width: 150px;
        font-size: 18px;
        font-weight: 600;
        border: none;
        outline: none;
        background-color: #008080;
        color: white;
        padding: 10px;
        cursor: pointer;
      }
    }
  }
}

