.modal {
  position: relative;
  min-height: 100vh !important;
  z-index: 100000 !important;
  .single-order {
    position: absolute;
    height: 80% !important;
    width: 80%;
    padding: 32px 0px;
    background-color: #ffffff;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    position: relative;
    .single-container {
      width: calc(100% - 32px);
      height: calc(100% - 32px);
      padding: 16px;
      display: flex;
      flex-direction: column;
      align-items: center;
      overflow: auto;
      scrollbar-width: thin;
      gap: 16px;
      .icon-container {
        position: absolute;
        right: 10px;
        top: 10px;
        z-index: 1;
        .close-icon {
          font-size: 40px;
          &:hover {
            background-color: #ff611d;
            color: white;
            border-radius: 50%;
            cursor: pointer;
          }
        }
      }
      .top {
        width: calc(100% - 16px);
        padding: 8px;
        display: flex;
        gap: 16px;
        .left {
          flex: 2;
          width: calc(100% - 32px);
          padding: 16px;
          background-color: #f2f2f2;
          -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
          box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
          border-radius: 10px;
          .item {
            display: flex;
            justify-content: flex-end;
            gap: 20px;

            .details {
              width: 100%;
              .itemTitle {
                margin-bottom: 10px;
                color: #555;
              }
              .detailItem {
                width: 100%;
                text-align: left;
                margin-bottom: 10px;
                font-size: 14px;
                display: flex;
                justify-content: space-between;
                align-items: center;
                .itemKey {
                  font-weight: bold;
                  color: gray;
                  margin-right: 5px;
                }

                .itemValue {
                  font-weight: 300;
                }
              }
            }
          }
        }

        .right {
          flex: 1;
          width: calc(100% - 32px);
          padding: 16px;
          background-color: #f2f2f2;
          -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
          box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
          border-radius: 10px;
          .icon-container {
            position: absolute;
            right: -20px;
            .close-icon {
              font-size: 40px;
              &:hover {
                background-color: #ff611d;
                color: white;
                border-radius: 50%;
                cursor: pointer;
              }
            }
          }
          .item {
            display: flex;
            justify-content: flex-end;
            gap: 20px;

            .details {
              width: 100%;
              .itemTitle {
                margin-bottom: 10px;
                color: #555;
              }
              .detailItem {
                width: 100%;
                text-align: left;
                margin-bottom: 10px;
                font-size: 14px;
                display: flex;
                justify-content: space-between;
                align-items: center;
                .itemKey {
                  font-weight: bold;
                  color: gray;
                  margin-right: 5px;
                }

                .itemValue {
                  font-weight: 300;
                }
              }
            }
          }
        }
      }

      .bottom {
        width: calc(100% - 32px);
        padding: 16px;
        background-color: #f3f3f3;
        -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
        box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
      }

      .title {
        font-size: 18px;
        margin-bottom: 20px;
        font-weight: 600;
      }

      .styled-table {
        background-color: #f3f3f3;
        width: 100%;
        border-collapse: collapse;
        min-width: 400px;
        thead tr {
          background-color: #ff611d;
          color: #ffffff;
          text-align: center;
          font-size: 18px;
        }
        tbody {
          text-align: center;
          font-weight: bold;
          :hover {
            color: #ff611d;
            background-color: #f3f3f3;
            cursor: pointer;
          }
        }

        th,
        td {
          padding: 12px 15px;
          img {
            width: 80px;
            height: 80px;
            object-fit: cover;
            border-radius: 50%;
          }
          object-fit: cover;
        }

        tbody tr:last-of-type {
          border-bottom: 2px solid #ff611d;
        }

        .icon-delete {
          margin-right: 30px;
          color: red;
          font-size: 30px;
          cursor: pointer;
        }

        .icon-edit {
          margin-right: 30px;
          color: #ff611d;
          font-size: 30px;
          cursor: pointer;
        }
      }
    }
  }
}

